import React, { Component } from 'react';
import AppBar from 'react-toolbox/lib/app_bar';
import styles from './App.pcss';
import { withRouter, Route } from "react-router-dom";
import Home from "components/pages/Home";
import TheoriesShow from "components/theories/Show";
import IssuesShowAll from "components/issues/ShowAll";
import IssuesShow from "components/issues/Show";
import UsersShow from "components/users/Show";

import {IconMenu, MenuItem, MenuDivider } from 'react-toolbox/lib/menu';

const MenuTest = () => (
  <IconMenu icon='more_vert' position='topRight' menuRipple>
    <MenuItem value='download' icon='get_app' caption='Download' />
    <MenuItem value='help' icon='favorite' caption='Favorite' />
    <MenuItem value='settings' icon='open_in_browser' caption='Open in app' />
    <MenuDivider />
    <MenuItem value='signout' icon='delete' caption='Delete' disabled />
  </IconMenu>
);

class App extends Component {
  goHome = ()=>{
    this.props.history.push('/apoe4/')
  }
  render() {
    return (
      <div>
        <AppBar className={styles.appBar} title={<h1 onClick={this.goHome}>ApoE4</h1>}>
          <MenuTest />
        </AppBar>
        <div>
          <Route exact path="/apoe4/" component={Home} />
          <Route exact path="/apoe4/theories/:id" component={TheoriesShow} />
          <Route exact path="/apoe4/theories/:id/issues" component={IssuesShowAll} />
          <Route exact path="/apoe4/theories/:id/issues/:id" component={IssuesShow} />
          <Route exact path="/apoe4/users/:id" component={UsersShow} />
        </div>
      </div>
    );
  }
}

export default withRouter(App);



